import './App.css';

import Main from './component/main';
import Festival from './data/festival'

import { useEffect, useRef, useState } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Detail from './component/detail';





function App() {
  const [ftv, setFtv] = useState([]);
  const [bottomDe, setBottomDe] = useState(0);
  const [clickItem, setClickItem] = useState();
  const [id, setId] = useState(); 
  const [detailInfo, setDetailInfo] = useState();
  const [year, setYear] = useState('2021');
  const [mon, setMon] = useState(1);
  const [area, setArea] = useState('');
  // const yearRef = useRef();
  // const monRef = useRef();
  // const areaRef = useRef();
  // const yearChange = () => setYear(yearRef.current.value)
  // const monChange = () => setMon(monRef.current.value) 
  const areaChange = (value) => setArea(value)
  
  const minus = (mon) => setMon(mon <= 1 ? mon = 12 : mon - 1)
  const plus = (mon) => setMon(mon >= 12 ? mon = 1 : mon + 1)
  


  const detail = (item) => {
        
        setClickItem(item)
        
        
        
        
        festival   
        .detailData(year, mon, area, item.contentid)
        .then(detailInfo => setDetailInfo(detailInfo));
        
        

    } 

    

    useEffect( () => {  
      
    festival   
    .festivalData(year, mon, area)
    .then(ftv => setFtv(ftv)); 
      
    }
      ,[area, year, mon])

     
      const festival = new Festival() 
  return (
      <BrowserRouter>
          <Route path="/" component={Main} exact={true}>
             <Main ftv={ftv} festival={festival}  onClickDetail={detail} bottomDe={bottomDe}  id={id} year={year} mon={mon} area={area} areaChange={areaChange} minus={minus} plus={plus} />
          </Route>
          
          <Route path="/detail" component={Detail}>
            <Detail  clickItem={clickItem} detailInfo={detailInfo} />
          </Route>
       
      </BrowserRouter>
  );
}

export default App;
