import axios from 'axios';

class Festival {
    constructor() {
        this.festival = axios.create({
            baseURL: 'https://festivalprojectapp.herokuapp.com/http://api.visitkorea.or.kr/openapi/service',
        });
    }
    
    async festivalData(year, mon, area) {
      const areaNumber = area == 0 ? area = '' : area
      const startDate = Number(year + (mon < 10 ? '0' : '') + mon + '01')
      
      const response = await this.festival.get("/rest/KorService/searchFestival?serviceKey=L2W%2FBNzWhCoJV%2F8gq1%2BNd%2Fhsm%2BylsyWMJxDnD3ZzIx3%2FltSKOKVOSXIfqdEBtVqJGSXC%2BPBranUO%2FrNfskcUAw%3D%3D", {
          params: {
            numOfRows: 500,
            pageNo: 1,
            _type: "json",
            arrange: "P",
            listYN: "Y",
            eventStartDate: startDate,
            MobileOS: "ETC",
            MobileApp: "AppTest",
            areaCode: areaNumber,
          },
        });
        return response.data.response.body.items.item;
    }   


    async detailData(year, mon, area, id) {
      const areaNumber = area == 0 ? area = '' : area
      const startDate = Number(year + (mon < 10 ? '0' : '') + mon + '01')
      
      const response = await this.festival.get("/rest/KorService/detailCommon?serviceKey=L2W%2FBNzWhCoJV%2F8gq1%2BNd%2Fhsm%2BylsyWMJxDnD3ZzIx3%2FltSKOKVOSXIfqdEBtVqJGSXC%2BPBranUO%2FrNfskcUAw%3D%3D", {
          params: {
            numOfRows: 1,
            pageNo: 1,
            _type: "json",
            arrange: "P",
            listYN: "Y",
            eventStartDate: startDate,
            MobileOS: "ETC",
            MobileApp: "AppTest",
            areaCode: areaNumber,
            contentId: id,
            defaultYN: "Y",
            firstImageYN: "Y",
            areacodeYN: "Y",
            catcodeYN: "Y",
            addrinfoYN: "Y",
            mapinfoYN: "Y",
            overviewYN:  "Y",
          },
        });
        return response.data.response.body.items.item;
    }   
}

export default Festival;