import React, { useEffect } from 'react';
import styles from './areaCode.module.css';

const AreaCode = ({areaValue, areaName, areaChange, area}) => {
            

            return (
                <li className={area == areaValue ? styles.active : styles.arealist} onClick={(e) => areaChange(e.target.value)} value={areaValue}>{areaName}</li>
            )

            
};

export default AreaCode;