import React, { useRef, useState } from 'react';
import AreaCode from './areaCode';
import Detail from './detail';
import FestivalCreate from './festivalCreate';
import styles from './main.module.css';
import PageNumber from './pageNumber';

const Main = ({onClickDetail, bottomDe, clickItem, ftv, year, mon, detailInfo, area, yearChange, monChange, areaChange, minus, plus}) => {
    
    const areaCode = [
        {name: '전국', value: ''},
        {name: '서울', value: '1'},
        {name: '인천', value: '2'},
        {name: '대전', value: '3'},
        {name: '대구', value: '4'},
        {name: '광주', value: '5'},
        {name: '부산', value: '6'},
        {name: '울산', value: '7'},
        {name: '세종시', value: '8'},
        {name: '경기도', value: '31'},
        {name: '강원도', value: '32'},
        {name: '충청북도', value: '33'},
        {name: '충청남도', value: '34'},
        {name: '경상북도', value: '35'},
        {name: '경상남도', value: '36'},
        {name: '전라북도', value: '37'},
        {name: '전라남도', value: '38'},
        {name: '제주도', value: '39'},
        
    ];
    const pageNum = []
    let totalCount = ftv.length
    let numOfRows = 10
    for( let i = 0; numOfRows * i < totalCount; i ++) {
        pageNum.push(i)
    }
    console.log(pageNum)
   


    // const [currentPage, setCurrentPage] = useState(1);
    // const [postsPerPage, setPostsPerPage] = useState(10);
    
    // const indexOfLast = currentPage * postsPerPage;  //10 = 1 * 10
    // const indexOfFirst = indexOfLast - postsPerPage; // 0 = 10 - 10
    // const currentPosts = (num) => {
    //   let currentPosts = 0;
    //   currentPosts = num.slice(indexOfFirst, indexOfLast); //0,10
    //   return currentPosts;
    // }

    return (
        <>
            <div className={styles.selectbox}>
                <ul className={styles.area}  >
                    {
                        areaCode.map((list, index) => <AreaCode key={index} areaName={list.name} area={area} areaValue={list.value} areaChange={areaChange} />) 
                    }

                </ul>
            </div>
            <div className={styles.selectDate}>
                <button className={styles.arrowLeft} onClick={()=> minus(mon)}><i className="fas fa-caret-left"></i></button>
                <p className={styles.arrowCenter}>{mon + '월'}</p>
                <button className={styles.arrowRight} onClick={()=> plus(mon)}><i className="fas fa-caret-right"></i></button>
            </div>
            
            <ul className={styles.ulList}>
                {   ftv && (ftv.length > 1 
                        ? ftv.map((item, index)=>  
                                  (item.eventstartdate <= Number(year + (mon < 10 ? '0' : '') + mon + '01')  && item.eventenddate >= Number(year + mon + '01')) && 
                                <FestivalCreate key={index} item={item} onClickDetail={onClickDetail} /> 
                        )

                        :  <FestivalCreate item={ftv} onClickDetail={onClickDetail} /> )
                       
                    
                }



            </ul>
            {/* <ul className={styles.pagelist}>
            {
                pageNum.map((num, index)=> <PageNumber key={index} num={num} /> )
            }
            </ul> */}
        </>
    );
};

export default Main;