import React, { useEffect, useRef, useState } from 'react';
import styles from './detail.module.css';


const Detail = ({clickItem, detailInfo}) => {
    const { title, addr1, eventstartdate, eventenddate, firstimage, tel } = clickItem;
    const detailRef = useRef();
    
    const date1 = String(eventstartdate)
        const year1 = date1.substring(0, 4)
        const month1 = date1.substring(4, 6)
        const day1 = date1.substring(6, 8)
        const dateRefresh1 = year1 + '.' + month1 + '.' + day1;  
        
        const date2 = String(eventenddate)
        const year2 = date2.substring(0, 4)
        const month2 = date2.substring(4, 6)
        const day2 = date2.substring(6, 8)
        const dateRefresh2 = year2 + '.' + month2 + '.' + day2;  
 
        let date = new Date();
        let year = date.getFullYear()              
        let month = (1 + date.getMonth())         
        month = month >= 10 ? month : '0' + month
        let day = date.getDate()                   
        day = day >= 10 ? day : '0' + day 
        const currentDate = year  + month + day   
        
    
    
    return ( 
        <div className={styles.wrap}>
            {
                (currentDate >= eventstartdate && currentDate <= eventenddate) ? <span className={styles.ing}>진행중</span> : <span className={styles.end}>종료됨</span>
            }
            
            <h2 className={styles.title} >{title}</h2>
            <div className={styles.info}>
                <p className={styles.infoLeft}> {addr1} </p>
                <p>{dateRefresh1} ~ {dateRefresh2}</p>
            </div>
            <div ref={detailRef} className={styles.bottomDetail}> 
                <img className={styles.img} src={firstimage} alt=""/>
                <div>
                    <div className={styles.exbox}>
                        <p className={styles.detailInfo}>상세정보</p>
                        <p className={styles.tel}> 전화번호 : {tel}</p>
                    </div>
                    <p className={styles.explanation}>{detailInfo && detailInfo.overview}</p>
                    
                </div>
            </div>
        </div>
    );
};

export default Detail;